.description {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
}

.name {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.job,
.job_description {
    display: flex;
    align-items: center;
}

.job_description b {
    display: inline-block;
    min-width: 80px;
}

.job b {
    display: inline-block;
    min-width: 150px;
}

.subtitle {
    padding: 0.3rem 0.3rem 0.3rem 1rem;
}

.btn {
    display: flex;
    justify-content: flex-end;
}

.subtitle b {
    display: inline-block;
    min-width: 100px;
}

.about b {
    display: inline-block;
    min-width: 90px;
}

